/*
GLOBAL LAYOUT STYLES
Regroup all views and define their structure.
*/
::selection {
  background: #ee6f57;
  color: #f6f5f5; }

::-webkit-scrollbar {
  width: 5px;
  z-index: 0; }

/* scrollbar width */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(238, 238, 238, 0.6); }

/* Track */
::-webkit-scrollbar-thumb {
  background: #ee6f57;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb:hover {
  background: #0087bd; }

/* Handle on hover */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

